@import "@webpixels/css/core/functions";

@import "./_variables";
@import "./_iPhone-zoom-fix";

@import "@webpixels/css/base";
@import "@webpixels/css/forms";
@import "@webpixels/css/components";
@import "@webpixels/css/utilities";

body {
	font-family: var(--x-font-sans-serif);
}

a {
	color: $black;
}

.btn-circle {
	width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    font-size: 12px;
    line-height: 1.42857;

    &-xl {
    	display: flex;
	    align-items: center;
	    justify-content: center;
	    width: 50px;
	    height: 50px;
	    border-radius: 35px;
	    font-size: 32px;
	    line-height: 1.33;
	}
}

.ajax-loader {
	.lds-default {
		display: inline-block;
		position: relative;
		width: 80px;
		height: 80px;

		div {
			position: absolute;
			width: 6px;
			height: 6px;
			background: #fff;
			border-radius: 50%;
			animation: lds-default 1.2s linear infinite;

			&:nth-child(1) {
				animation-delay: 0s;
				top: 37px;
				left: 66px;
			}

			&:nth-child(2) {
				animation-delay: -0.1s;
				top: 22px;
				left: 62px;
			}

			&:nth-child(3) {
				animation-delay: -0.2s;
				top: 11px;
				left: 52px;
			}

			&:nth-child(4) {
				animation-delay: -0.3s;
				top: 7px;
				left: 37px;
			}

			&:nth-child(5) {
				animation-delay: -0.4s;
				top: 11px;
				left: 22px;
			}

			&:nth-child(6) {
				animation-delay: -0.5s;
				top: 22px;
				left: 11px;
			}

			&:nth-child(7) {
				animation-delay: -0.6s;
				top: 37px;
				left: 7px;
			}

			&:nth-child(8) {
				animation-delay: -0.7s;
				top: 52px;
				left: 11px;
			}

			&:nth-child(9) {
				animation-delay: -0.8s;
				top: 62px;
				left: 22px;
			}

			&:nth-child(10) {
				animation-delay: -0.9s;
				top: 66px;
				left: 37px;
			}

			&:nth-child(11) {
				animation-delay: -1s;
				top: 62px;
				left: 52px;
			}

			&:nth-child(12) {
				animation-delay: -1.1s;
				top: 52px;
				left: 62px;
			}
		}

		&.dark {
			div {
				background: #888;
			}
		}

		&.small {
			transform: scale(.5);
		}
	}
}

@keyframes lds-default {
	0%, 20%, 80%, 100% {
		transform: scale(1);
	}
	50% {
		transform: scale(1.5);
	}
}


@media (max-width:767px) {
	.navbar-nav .dropdown-menu {
		border: none;
	}

	.nav-link.dropdown-toggle {
		padding: 10px 0 0;
	}
}